.card {
    background: #fff;
    border-radius: 2px;
    height: 325px;
    margin: 4rem;
    position: relative;
    width: 325px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    overflow: hidden;
    margin-top: 5%;
    text-align: center;
    /* background:#6CADDC; */
  }
  .card > .img-container > img {
    width: 90%
  }
